@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Ubuntu+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  max-width: 100vw;
}

body, html {
  font-family: 'Josefin Sans', sans-serif;
  height: 100%;
  background: black;
  max-width: 100vw;
  scroll-behavior: smooth;
}

a {
  color: black;
}

.mainPage {
  color: white;
  background: black;
  max-width: 100vw;
}

.add {
  margin-top: 5vh;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}
.homePage {
    justify-content: start;
    max-width: 100vw;
    min-height: 100vh;
    color: white;
    background: black;
}

.projectsContainer {
  min-height: 100vh;
  margin-top: 0;
}

.typistText {
  margin-left: 2rem;
  /* margin-top: 5rem; */
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.homePage h1 {
    text-align: left;
    font-size: 4rem;
    font-weight: 100;
}

.textFlair {
    color: #16c824;
    font-style: italic;
}

.textFlairMono {
  color: #16c824;
  font-family: monospace;
}

