.Typist .Cursor {
    display: inline-block; }
    .Typist .Cursor--blinking {
      opacity: 1;
      -webkit-animation: blink 1s linear infinite;
              animation: blink 1s linear infinite; }
  
  @-webkit-keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } 
  }
  
  @keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } 
  }

.downArrow {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  margin-left: -1%;
  right: 0;
  -webkit-animation: pulse 1s linear infinite;
          animation: pulse 1s linear infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(100%);
            transform: scale(100%)
  }
  50% {
    -webkit-transform: scale(110%);
            transform: scale(110%)
  }
  100% {
    -webkit-transform: scale(100%);
            transform: scale(100%)
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(100%);
            transform: scale(100%)
  }
  50% {
    -webkit-transform: scale(110%);
            transform: scale(110%)
  }
  100% {
    -webkit-transform: scale(100%);
            transform: scale(100%)
  }
}

