    min-width: 350px;
    max-width: 70vw;
    background: rgb(20, 20, 20);
    padding: 2rem;
    min-height: 350px;
    margin-top: 30px;
    color: black;
    background: rgb(240, 240, 240);
    /* border: 1px solid white; */
    /* transition: 0.3s ease; */
}

.project h1 {
    font-size: 3rem;
}

.projectPage {
    /* height: 100%; */
    display: grid;
    place-items: center;
    align-content: center;
}

.projectPageTitle {
    color: #16c824;
    font-size: 3rem;
    text-align: center;
    margin: auto;
}

.project img {
    width: 75%;
    border-radius: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
}

.loader {
