.project-details{
    margin: auto;
    /* margin-top: 3rem; */
    height: 100%;
    width: 60vw;
    text-align: left;
    display: grid;
    place-items: center;
    align-content: center;
    -webkit-animation: appear 1s linear 1;
            animation: appear 1s linear 1;
}

@-webkit-keyframes appear {
    from {opacity: 0%;}
    to {opacity : 100%;}
  }

@keyframes appear {
    from {opacity: 0%;}
    to {opacity : 100%;}
  } 

.project-details a {
    color: white;
    line-height: 2rem;
}

.project-details img{
    width: 100%;
    border-radius: 0;
}

.pager {
    width: 60vw;
    color: lightgray;
    left: 0;
    display: grid;
    grid-auto-flow: column;
    text-align: center;
}

.pager a {
    color: white;
}
.contactPage {
    height: 100vh;
    display: grid;
    place-items: center;
    align-content: center;
}

.contactPage a {
    color: white;
}

.contactPage h1 {
    margin-bottom: 3rem;
    color: #16c824;
}

.contactPage p {
    font-size: 1.2rem;
    line-height: 2;
    color: white;
}
.header-container {
    position: fixed;
    right: 0;
    margin-top: 1rem;
    display: grid;
    place-items: right;
}

.burger {
    display: none;
    z-index: 2;
    position: absolute;
    cursor: pointer;
}

header {
    display: grid;
    grid-auto-flow: row;
    justify-content: right;
    align-items: right;
    text-align: right;
    grid-gap: .5rem;
    gap: .5rem;
    z-index: 1;
    padding: .3rem;
}

.loginButton {
    display: none;
}

.headerButtons {
    padding: .4rem;
    text-decoration: none;
    background-color: black;
    color: white;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}
  
.headerButtons:hover {
    color: black;
    background-color: white;
}

[aria-current="page"] {
    font-weight: bold;
    color: #16c824;
}

@media (max-width: 1000px) {
    header {
        min-width: 100%;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
        grid-auto-flow: row;
        align-content: center;
        min-height: 100vh;
        -webkit-transition: 0.5s ease;
        transition: 0.5s ease;
        background: black;
        justify-content: center;    
    }

    header a {
        font-size: 2rem;
    }
    
    .burger {
        display: block;
    }

    header.visible {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
        z-index: 100;
    }

    main {
        padding: 5vh, 5vh;
    }
}

.login {
    height: 100vh;
    display: grid;
    place-items: center;
    align-content: center;
}

.login button {
    background: black;
    border: 1px solid lightgray;
    color: white;
    padding: 0.8rem;
}

img {
    width: 50px;
    margin: 1rem 0;
    border-radius: 50%;
}
input, textarea {
    font-size: 1rem;
    padding: .6rem;
    margin: 2rem 0;
    display: block;
}

textarea {
    min-width: 60vw;
    min-height: 200px;
}

.textInputSmall {
    width: 40vw;
}

.editPage {
    background: #151515;
    height: 100%;
    width: 80vw;
    margin: auto;
    padding: 3rem;
    margin-top: 4rem;
}

.checks {
    display: grid;
    grid-template-columns: 1fr, 4fr;
    margin: .4rem 0;
    padding: .6rem;
}

.edit .img {
    width: 5rem;
}

.edit label {
    padding: 0;
    display: inline-block;
}

select {
    margin-bottom: 1rem;
}

.uploaderImage {
    border-radius: 0%;
}

.project-images {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    justify-content: start;
    grid-gap: .2rem;
    gap: .2rem;
    margin-bottom: 1rem;
}

.project-images > div {
    display: grid;
    place-items: center;
    position: relative;
}

.project-images img {
    width: 100%;
}

.button {
    border: 1px solid black;
    max-width: 130px;
    padding: .4rem;
    text-align: center;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.button:hover {
    background: black;
    color: white;
}
